'use strict';

// MAIN //

/**
* Initializes a cache for pooled typed array buffers.
*
* @private
* @param {NonNegativeInteger} n - base-2 logarithm of the maximum typed array size
* @returns {ArrayArray} initialized cache
*/
function pool( n ) {
	var out;
	var i;

	out = [];
	for ( i = 0; i < n+1; i++ ) {
		out.push( [] );
	}
	return out;
}


// EXPORTS //

export default pool;
